.map {
        -webkit-text-size-adjust: 100%;
            -webkit-tap-highlight-color: transparent;
            font-family: "Open Sans", sans-serif;
            line-height: 1.428571429;
            color: #333;
            font-size: 14px;
            box-sizing: border-box;
            position: relative;
            min-height: 1px;
            padding-right: 15px;
            padding-left: 15px;
            float: left;
            width: 50%;
            margin-top: 0px;
}

.square {
        -webkit-text-size-adjust: 100%;
            -webkit-tap-highlight-color: transparent;
            font-family: "Open Sans", sans-serif;
            line-height: 1.428571429;
            color: #333;
            font-size: 14px;
            box-sizing: border-box;
            position: relative;
}

